.highlight-share__share-box {
  position: absolute;
  left: -9999px; /* Hidden to start */
  top: -9999px;

  /* Avoids measurement weirdness */
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  box-sizing:border-box;

  /* So the box can fade in */
  opacity: 0;
  -webkit-transition: opacity .2s ease-in-out;
  transition: opacity .2s ease-in-out;

  /* Theoretically should make sure it's never buried */
  z-index: 9999;

  /* Visual styles — edit these as needed */
  display: block;
  width: 50px;
  height: 50px;
  margin: 0;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 -1px 8px rgba(255,255,255,.7);
  background: rgba(0,0,0,.9);
  -webkit-transform: translate3d(-50%, -130%, 0);
  -ms-transform: translate3d(-50%, -130%, 0);
  transform: translate3d(-50%, -130%, 0);
}

.highlight-share__share-box:after {
  content: '';
  position: absolute;
  top: 100%;
  left: 15px;
  width: 20px;
  height: 5px;
  border-top: 5px solid rgba(0,0,0,.9);
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
}

.highlight-share__share-box.highlight-share__is-active {
  opacity: 1;
}

/* Base styles for buttons */
.highlight-share__share-btn,
.highlight-share__share-btn--twitter {
  display: block;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  border: 0;
  background: transparent;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  opacity: .8;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.highlight-share__share-btn:hover,
.highlight-share__share-btn:active,
.highlight-share__share-btn:focus,
.highlight-share__share-btn--twitter:hover,
.highlight-share__share-btn--twitter:active,
.highlight-share__share-btn--twitter:focus {
  outline: none;
  opacity: 1;
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}

